.session {
  background-color: white;
  display: flex;
  flex-direction: column;
  position: relative;
  transition: box-shadow 0.2s;
  box-shadow: 0 0 15px 0px rgb(0, 0, 0, 0.1);
  border-radius: 15px;
  height: 100%;

  width: 100%;
  overflow: hidden;
}

.sessionImg {
  width: 100%;
  object-fit: cover;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  overflow: hidden;
}

.sessionTitle {
  line-height: 1.75rem;
  font-weight: 500;
  text-transform: uppercase;
}

.sessionTitleDiv:after {
  display: block;
  content: "";
  height: 1px;
  margin-bottom: 24px;
  background-image: linear-gradient(
    90deg,
    rgba(0, 0, 0, 0) 0%,
    var(--secondary-color) 40%,
    var(--secondary-color) 60%,
    rgba(0, 0, 0, 0) 100%
  );
}

.sessionDescription {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  background-color: white;
  border-radius: 15px;
  padding: 15px;
  box-sizing: border-box;

  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 1;
}

.sessionLinkContainer {
  margin-bottom: 1rem;
}

.sessionLink {
  transition: 0.2s;
  text-decoration: none;
  font-size: 1em;
  color: var(--link-color);
  padding: 0.75rem 1.4rem 0.75rem 1.4rem;
  border-radius: 30px;
  background-color: var(--secondary-color);
}

.sessionLink:hover {
  background-color: var(--tertiary-color);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.178);
}

.session:hover {
  transition: box-shadow 0.2s;
  box-shadow: 0 0 15px 0px rgb(0, 0, 0, 0.5);
}

.show {
  transition: 0.4s, transform 0.4s;
  top: 0;
}

.hide {
  transition: 0.2s, transform 0.2s;
  top: 100%;
}

@media (width < 768px) {
  .session {
    margin-bottom: 2rem;
    height: 25rem;
  }
}
