.about {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.about h1 {
  text-align: center;
}

.aboutMeDesc {
  max-width: 90%;
  margin: auto;
  align-items: center;
  display: flex;
  justify-content: space-between;
  gap: 4em;
}

.aboutMeDesc img {
  width: 15rem;
  height: 15rem;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0 0 15px 0px rgb(0, 0, 0, 0.2);
}

@media (width < 768px) {
  .aboutMeDesc {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    gap: 2rem;
  }
}
