.video {
  top: 0;
  left: 0;
  width: 100svw;
  max-width: 100%;
  height: 90svh;
  object-fit: cover;
  position: absolute;
  z-index: -1;
  filter: brightness(0.5);
}

.hero {
  height: calc(100vh - 4rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  margin-bottom: 3rem;
}

.firstLine,
.secondLine {
  transition: 0.6s ease-in;
  opacity: 1;
}

.hero h1 {
  font-size: 5rem;
  font-weight: 300;
  line-height: 5rem;
  text-align: center;
  color: white;
}

.hero .secondLine {
  font-size: 2rem;
}

.heroLearnMore {
  transform: translateY(-50%);
  position: absolute;
  top: 90%;
}

.heroScroll {
  line-height: 0;
  cursor: pointer;
  font-family: Montserrat;
  background-color: var(--tertiary-color);
  font-size: 1.3em;
  padding: 1.5em 2em 1.5em 2em;
  border-radius: 3rem;
  border: 3px solid white;
  transition: 0.2s;
}

.heroScroll > span {
  vertical-align: sub;
  font-size: 3rem;
}

.heroScroll:hover {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.178);
}

.heroScroll:active {
  scale: 0.95;
}
